/*header*/
header{
  justify-content: center;
  padding-top: 70px;
  background-image: url("../img/sky-bg.png");
  background-repeat: no-repeat;
  background-position: center -339px;
}

.container--head{
    flex-direction: column;
    align-content: center;
}

.logo{
    text-align: center;
}

/*top menu*/

.top_menu{
    padding: 10px 0 0 0px;
}

.menu{
    flex-direction: column;
    
}
.menu_item{
    display: flex;
    justify-content: center;
}


